.msgComponent {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0;
  flex-wrap: nowrap;
  max-width: 100%;
}
.msgComponent:global.c-msg-component .c-msg-avatar {
  min-width: fit-content;
}
.msgComponent:global.c-msg-component .c-msg-content {
  flex: 1;
  min-width: 0;
}
.msgComponent:global.c-msg-component .c-msg-name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 10px 8px;
  min-width: 0;
}
.msgComponent:global.c-msg-component .c-msg-line {
  margin-bottom: 8px;
}
.msgComponent:global.c-msg-component .c-msg-line:last-child {
  margin-bottom: 0;
}
.msgComponent:global.c-msg-component .c-msg-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-wrap: nowrap;
}
.msgComponent:global.c-msg-component .c-msg-timestamp {
  padding: 0 8px;
  align-self: flex-end;
  min-width: 0;
}
.msgComponent:global.c-message-left {
  margin-left: 0;
  margin-right: auto;
}
.msgComponent:global.c-message-left .c-msg-content .c-msg-text-line {
  background-color: var(--message-background-color-left);
  color: var(--message-color-left);
  border-radius: var(--message-border-radius-left);
}
.msgComponent:global.c-message-right {
  margin-left: auto;
  margin-right: 0;
  flex-direction: row-reverse;
}
.msgComponent:global.c-message-right .c-msg-avatar,
.msgComponent:global.c-message-right .c-msg-name {
  display: none;
}
.msgComponent:global.c-message-right .c-msg-content .c-msg-text-line {
  background-color: var(--message-background-color-right);
  color: var(--message-color-right);
  border-radius: var(--message-border-radius-right);
}

.messageListContainer {
  position: relative;
  height: 100%;
}
.messageListContainer :global .c-message-list-component {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}
.messageListContainer :global .c-message-list-component:-webkit-scrollbar {
  display: none; /* Chrome, Safari */
}
.messageListContainer :global .c-message-list-component .c-message-item {
  padding-bottom: 16px;
}
.messageListContainer :global .c-chatbox-scroll-to-end-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
  z-index: 1000;
}
.messageListContainer :global .c-message-list-empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
}