.timeLabel {
  display: flex;
  gap: 2px;
  align-items: center;
  width: fit-content;
}
.timeLabel :global .c-time-label-icon {
  object-fit: contain;
  color: var(--time-label-icon-color);
  width: var(--time-label-icon-width);
  height: var(--time-label-icon-height);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  flex-wrap: nowrap;
}
.timeLabel :global .c-time-label-value {
  margin: 0;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-family: var(--font-family);
  font-size: var(--time-label-font-size);
  font-weight: var(--time-label-font-weight);
  line-height: var(--time-label-line-height);
  letter-spacing: var(--letter-spacing-base);
}
.timeLabel:global.c-time-label-error {
  color: var(--time-label-error-color);
}
.timeLabel:global.c-time-label-error .c-time-label-icon {
  color: var(--time-label-error-color);
}
.timeLabel:global.c-time-label-icon-start {
  flex-direction: row-reverse;
}