:root {
  --color-red-500: black;
  --font-family: "Pretendard", sans-serif;
  --font-size-base: 14px;
  --font-weight-base: 400;
  --line-height-base: 100%;
  --letter-spacing-base: 0;
  --color-base: #250F09;
  --button-border-color: #F0EFF1;
  --button-border-radius: 10px;
  --buttom-font-size: var(--font-size-base);
  --button-font-weight: var(--font-weight-base);
  --button-line-height: var(--line-height-base);
  --button-active-bg: #f5f5f5;
  --button-active-border: #FF627B;
  --button-active-text: #3e3e3e;
  --button-font-size-small: 14px;
  --button-font-size-medium: 16px;
  --button-font-size-large: 18px;
  --button-icon-gap-small: 6px;
  --button-icon-gap-medium: 8px;
  --button-icon-gap-large: 10px;
  --button-padding-small: 4px 12px;
  --button-padding-medium: 11px;
  --button-padding-large: 12px 24px;
  --button-primary-bg: #FFFFFF;
  --button-primary-bg-hover: #FFFFFF;
  --button-primary-bg-disabled: #9C9EB0;
  --button-primary-text: #9C9EB0;
  --button-primary-text-hover: #FF627B;
  --button-primary-text-disabled: #FFFFFF;
  --button-primary-border: #F0EFF1;
  --button-primary-border-hover: #FF627B;
  --button-primary-border-disabled: #F0EFF1;
  --button-secondary-bg: #f5f5f5;
  --button-secondary-bg-hover: #ebebeb;
  --button-secondary-bg-disabled: #d7d7d7;
  --button-secondary-text: #3e3e3e;
  --button-secondary-text-disabled: #ffffff;
  --button-secondary-border: #ebebeb;
  --button-secondary-border-hover: #3e3e3e;
  --button-secondary-border-disabled: transparent;
  --button-link-bg: transparent;
  --button-link-bg-disabled: transparent;
  --button-link-text: #6c46e6;
  --button-link-text-hover: #449eee;
  --button-link-text-disabled: #a0bbf0;
  --button-link-border: transparent;
  --button-link-border-disabled: transparent;
  --button-link-underline-color: #6c46e6;
  --button-link-underline-color-hover: #449eee;
  --button-link-underline-color-disabled: transparent;
  --button-link-underline-height: 2px;
  --button-link-underline-width: calc(100% - 24px);
  --input-text-color: #250F09;
  --input-height: 40px;
  --input-padding: 16px 8px;
  --input-placeholder-color: #9C9EB0;
  --input-border-radius: 5px;
  --input-background-color: #ffffff;
  --input-border-color: #F0EFF1;
  --input-icon-width: 24px;
  --input-icon-height: 24px;
  --input-focus-text-color: #250F09;
  --input-focus-background-color: #ffffff;
  --input-focus-border-color: #FF627B;
  --input-error-text-color: #3e3e3e;
  --input-error-background-color: #ffffff;
  --input-error-border-color: #ea483d;
  --input-disabled-text-color: #9C9EB0;
  --input-disabled-background-color: #F0EFF1;
  --input-disabled-border-color: #F0EFF1;
  --input-font-size: 14px;
  --input-font-weight: 400;
  --input-line-height: 100%;
  --input-icon-padding: 16px;
  --textarea-text-color: #250F09;
  --textarea-font-size: 14px;
  --textarea-font-weight: 400;
  --textarea-line-height: 100%;
  --textarea-padding: 12px 8px;
  --textarea-placeholder-color: #9C9EB0;
  --textarea-border-radius: 5px;
  --textarea-background-color: #ffffff;
  --textarea-border-color: #F0EFF1;
  --textarea-focus-text-color: #250F09;
  --textarea-focus-background-color: #ffffff;
  --textarea-focus-border-color: #FF627B;
  --textarea-error-text-color: #3e3e3e;
  --textarea-error-background-color: #ffffff;
  --textarea-error-border-color: #ea483d;
  --textarea-disabled-text-color: #9C9EB0;
  --textarea-disabled-background-color: #F0EFF1;
  --textarea-disabled-border-color: #F0EFF1;
  --avatar-border-radius: 50%;
  --avatar-background-color: var(--color-red-500);
  --avatar-image-border-radius: 50%;
  --avatar-width-small: 40px;
  --avatar-height-small: 40px;
  --avatar-width-medium: 48px;
  --avatar-height-medium: 48px;
  --avatar-width-large: 64px;
  --avatar-height-large: 64px;
  --time-label-font-size: 12px;
  --time-label-font-weight: 400;
  --time-label-line-height: 1.5;
  --time-label-icon-width: 16px;
  --time-label-icon-height: 16px;
  --time-label-icon-color: #4e72f1;
  --time-label-error-color: #ff554a;
  --message-padding: 16px;
  --message-border-radius: 16px;
  --message-background-color: #ffffff;
  --message-color: #3e3e3e;
  --message-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  --message-border-color: #ebebeb;
  --message-font-size: var(--font-size-base);
  --message-font-weight: var(--font-weight-base);
  --message-line-height: var(--line-height-base);
  --message-letter-spacing: var(--letter-spacing-base);
  --message-background-color-left: #ffffff;
  --message-color-left: #3e3e3e;
  --message-border-radius-left: 16px 16px 16px 0;
  --message-background-color-right: #ffffff;
  --message-color-right: #3e3e3e;
  --message-border-radius-right: 16px 16px 0 16px;
  --message-image-width: 100px;
  --message-image-height: 100px;
  --message-image-border-radius: 16px;
  --message-image-border-color: transparent;
  --message-image-width-small: 100px;
  --message-image-height-small: 100px;
  --message-image-width-medium: 100px;
  --message-image-height-medium: 100px;
  --message-image-width-large: 100px;
  --message-image-height-large: 100px;
  --message-video-width: 100px;
  --message-video-height: 100px;
  --message-video-border-radius: 16px;
  --message-video-border-color: transparent;
  --message-video-blur: 3px;
  --top-info-width: 164px;
  --top-info-name-font-size: 14px;
  --top-info-name-font-weight: 600;
  --top-info-name-line-height: 1.14;
  --top-info-name-color: #3e3e3e;
  --top-info-info-font-size: 10px;
  --top-info-info-font-weight: 400;
  --top-info-info-line-height: 1.6;
  --top-info-info-color: #3e3e3e;
  --top-info-actions-width: 100%;
  --top-info-button-border-color: #F37021;
  --top-info-button-color: #F37021;
  --c-padding: 16px;
  --c-composer-padding: 0;
  --c-composer-background-color: transparent;
}

