  --button-primary-text-hover: #FF627B;
  --button-primary-text-disabled: #888888;
  --button-primary-border: #f37021;
  --button-primary-border-hover: #ff8c42;
  --button-primary-border-disabled: transparent;
  --button-secondary-bg: #2a2a2a;
  --button-secondary-bg-hover: #3a3a3a;
  --button-secondary-bg-disabled: #4a4a4a;
  --button-secondary-text: #e0e0e0;
  --button-secondary-text-disabled: #888888;
  --button-secondary-border: #4a4a4a;
  --button-secondary-border-hover: #6a6a6a;
  --button-secondary-border-disabled: transparent;
  --button-tertiary-bg: transparent;
  --button-tertiary-bg-hover: #2a2a2a;
  --button-tertiary-bg-disabled: #4a4a4a;
  --button-tertiary-text: #e0e0e0;
  --button-tertiary-text-disabled: #888888;
  --button-tertiary-border: transparent;
  --button-tertiary-border-hover: #4a4a4a;
  --button-tertiary-border-disabled: transparent;
  --button-link-bg: transparent;
  --button-link-bg-disabled: transparent;
  --button-link-text: #e0e0e0;
  --button-link-text-hover: #ffffff;
  --button-link-text-disabled: #888888;
  --button-link-border: transparent;
  --button-link-border-disabled: transparent;
  --button-link-underline-color: #f37021;
