  --input-border-radius: 16px;
  --input-background-color: #2f2f2f;
  --input-border-color: #2f2f2f;
  --input-icon-width: 24px;
  --input-icon-height: 24px;
  --input-focus-text-color: #f5f5f5;
  --input-focus-background-color: #1f1f1f;
  --input-focus-border-color: #f58d4d;
  --input-error-text-color: #f5f5f5;
  --input-error-background-color: #1f1f1f;
  --input-error-border-color: #ff554a;
  --input-disabled-text-color: #3e3e3e;
  --input-disabled-background-color: #1f1f1f;
  --input-disabled-border-color: #1f1f1f;
  --input-font-size: var(--font-size-base);
  --input-font-weight: var(--font-weight-base);
  --input-line-height: var(--line-height-base);
  --input-icon-padding: 36px;
  --textarea-text-color: #f5f5f5;
  --textarea-font-size: var(--font-size-base);
  --textarea-font-weight: var(--font-weight-base);
  --textarea-line-height: var(--line-height-base);
  --textarea-padding: 16px 8px;
  --textarea-placeholder-color: #9c9c9c;
  --textarea-border-radius: 16px;
  --textarea-background-color: #2f2f2f;
  --textarea-border-color: #2f2f2f;
  --textarea-focus-text-color: #f5f5f5;
  --textarea-focus-background-color: #000000;
  --textarea-focus-border-color: #f58d4d;
  --textarea-error-text-color: #f5f5f5;
  --textarea-error-background-color: #000000;
  --textarea-error-border-color: #ff554a;
  --textarea-disabled-text-color: #3e3e3e;
  --textarea-disabled-background-color: #1f1f1f;
