  --time-label-icon-height: 16px;
  --time-label-icon-color: #4e72f1;
  --time-label-error-color: #ff554a;
  --message-padding: 16px;
  --message-border-radius: 16px;
  --message-background-color: #ffffff;
  --message-color: #3e3e3e;
  --message-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  --message-border-color: #ebebeb;
  --message-font-size: var(--font-size-base);
  --message-font-weight: var(--font-weight-base);
  --message-line-height: var(--line-height-base);
  --message-letter-spacing: var(--letter-spacing-base);
  --message-background-color-left: #ffffff;
  --message-color-left: #3e3e3e;
  --message-background-color-right: #ffffff;
  --message-color-right: #3e3e3e;
  --message-image-width: 100px;
  --message-image-height: 100px;
  --message-image-border-radius: 16px;
  --message-image-border-color: transparent;
  --message-image-width-small: 100px;
  --message-image-height-small: 100px;
