.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  flex-wrap: nowrap;
  font-family: var(--font-family);
  font-size: var(--buttom-font-size);
  font-weight: var(--button-font-weight);
  line-height: var(--button-line-height);
  letter-spacing: var(--letter-spacing-base);
  border: 1px solid var(--button-border-color);
  border-radius: var(--button-border-radius);
  cursor: pointer;
  text-decoration: none;
  outline: none;
}
.button:active {
  background-color: var(--button-active-bg);
  border-color: var(--button-active-border);
  color: var(--button-active-text);
}
.button:global.c-button-primary {
  background-color: var(--button-primary-bg);
  border-color: var(--button-primary-border);
  color: var(--button-primary-text);
}
.button:global.c-button-primary:hover {
  background-color: var(--button-primary-bg-hover);
  border-color: var(--button-primary-border-hover);
  color: var(--button-primary-text-hover);
}
.button:global.c-button-primary.disabled, .button:global.c-button-primary:disabled {
  background-color: var(--button-primary-bg-disabled);
  border-color: var(--button-primary-border-disabled);
  color: var(--button-primary-text-disabled);
}
.button:global.c-button-secondary {
  background-color: var(--button-secondary-bg);
  border-color: var(--button-secondary-border);
  color: var(--button-secondary-text);
}
.button:global.c-button-secondary:hover {
  background-color: var(--button-secondary-bg-hover);
  border-color: var(--button-secondary-border-hover);
}
.button:global.c-button-secondary.disabled, .button:global.c-button-secondary:disabled {
  background-color: var(--button-secondary-bg-disabled);
  border-color: var(--button-secondary-border-disabled);
  color: var(--button-secondary-text-disabled);
}
.button:global.c-button-link {
  background-color: var(--button-link-bg);
  border-color: var(--button-link-border);
  color: var(--button-link-text);
  width: fit-content;
  position: relative;
}
.button:global.c-button-link::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 4px;
  transform: translateX(-50%);
  width: var(--button-link-underline-width);
  height: var(--button-link-underline-height);
  background-color: var(--button-link-underline-color);
}
.button:global.c-button-link:hover {
  color: var(--button-link-text-hover);
}
.button:global.c-button-link:hover::after {
  background-color: var(--button-link-underline-color-hover);
}
.button:global.c-button-link.disabled, .button:global.c-button-link:disabled {
  pointer-events: none;
  color: var(--button-link-text-disabled);
  background-color: var(--button-link-bg-disabled);
  border-color: var(--button-link-border-disabled);
}
.button:global.c-button-link.disabled::after, .button:global.c-button-link:disabled::after {
  background-color: var(--button-link-underline-color-disabled);
}
.button:global.c-button-small {
  padding: var(--button-padding-small);
  font-size: var(--button-font-size-small);
  gap: var(--button-icon-gap-small);
}
.button:global.c-button-medium {
  padding: var(--button-padding-medium);
  font-size: var(--button-font-size-medium);
  gap: var(--button-icon-gap-medium);
}
.button:global.c-button-large {
  padding: var(--button-padding-large);
  font-size: var(--button-font-size-large);
  gap: var(--button-icon-gap-large);
}
.button:global.c-button-icon-left {
  display: flex;
}
.button:global.c-button-icon-right {
  display: flex;
  flex-direction: row-reverse;
}
.button:global.disabled, .button:global:disabled {
  cursor: not-allowed;
}