.topInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
  flex-wrap: nowrap;
  width: 100%;
}
.topInfo :global .c-top-info-user-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-wrap: nowrap;
}
.topInfo :global .c-top-info-user-section .c-top-info-name-and-role {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 6px;
  flex-wrap: nowrap;
}