.message {
  width: 100%;
  padding: var(--message-padding);
  background-color: var(--message-background-color);
  color: var(--message-color);
  box-shadow: var(--message-box-shadow);
  border: 1px solid var(--message-border-color);
  border-radius: var(--message-border-radius);
  font-family: var(--font-family);
  font-size: var(--message-font-size);
  font-weight: var(--message-font-weight);
  line-height: var(--message-line-height);
  letter-spacing: var(--message-letter-spacing);
}

.messageImage {
  width: var(--message-image-width);
  height: var(--message-image-height);
  border-radius: var(--message-image-border-radius);
  border: 1px solid var(--message-image-border-color);
}
.messageImage img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: var(--message-image-border-radius);
}
.messageImage:global.c-message-image-small {
  width: var(--message-image-width-small);
  height: var(--message-image-height-small);
}
.messageImage:global.c-message-image-medium {
  width: var(--message-image-width-medium);
  height: var(--message-image-height-medium);
}
.messageImage:global.c-message-image-large {
  width: var(--message-image-width-large);
  height: var(--message-image-height-large);
}

.messageVideo {
  width: var(--message-video-width);
  height: var(--message-video-height);
  border-radius: var(--message-video-border-radius);
  border: 1px solid var(--message-video-border-color);
  overflow: hidden;
}
.messageVideo img {
  width: 100%;
  height: 100%;
  border-radius: var(--message-video-border-radius);
  object-fit: contain;
  filter: blur(var(--message-video-blur));
}
.messageVideo video {
  width: 100%;
  height: 100%;
  border-radius: var(--message-video-border-radius);
}