.avatar {
  background-color: var(--avatar-background-color);
  border: 1px solid var(--avatar-border-color);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  flex-wrap: nowrap;
}
.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.avatar:global.c-avatar-circle {
  border-radius: var(--avatar-border-radius);
}
.avatar:global.c-avatar-circle img {
  border-radius: var(--avatar-border-radius);
}
.avatar:global.c-avatar-square {
  border-radius: 0;
}
.avatar:global.c-avatar-square img {
  border-radius: 0;
}
.avatar:global.c-avatar-small {
  width: var(--avatar-width-small);
  height: var(--avatar-height-small);
}
.avatar:global.c-avatar-medium {
  width: var(--avatar-width-medium);
  height: var(--avatar-height-medium);
}
.avatar:global.c-avatar-large {
  width: var(--avatar-width-large);
  height: var(--avatar-height-large);
}