.textareaContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-wrap: nowrap;
  border-radius: var(--textarea-border-radius);
  border: 1px solid var(--textarea-border-color);
  padding: var(--textarea-padding);
}
.textareaContainer:hover {
  border: 1px solid var(--textarea-focus-border-color);
  outline: 0;
}
.textareaContainer:focus {
  color: var(--textarea-focus-text-color);
  background-color: var(--textarea-focus-background-color);
  border: 1px solid var(--textarea-focus-border-color);
  outline: 0;
}
.textareaContainer textarea {
  font-family: var(--font-family);
  font-size: var(--textarea-font-size);
  font-weight: var(--textarea-font-weight);
  line-height: var(--textarea-line-height);
  letter-spacing: var(--letter-spacing-base);
  width: 100%;
  color: var(--textarea-text-color);
  background-color: var(--textarea-background-color);
  transition: border-color 0.3s ease;
  border: none;
  box-sizing: border-box;
  scrollbar-width: none;
  line-height: var(--textarea-line-height);
}
.textareaContainer textarea ::placeholder {
  color: var(--textarea-placeholder-color);
}
.textareaContainer textarea:hover, .textareaContainer textarea:focus {
  border: none;
  outline: none;
}
.textareaContainer textarea:global:disabled, .textareaContainer textarea:global.c-textarea-disabled {
  color: var(--textarea-disabled-text-color);
  background-color: var(--textarea-disabled-background-color);
  border-color: var(--textarea-disabled-border-color);
  cursor: not-allowed;
  pointer-events: none;
}
.textareaContainer textarea:global.c-textarea-error {
  color: var(--textarea-error-text-color);
  background-color: var(--textarea-error-background-color);
  border: 1px solid var(--textarea-error-border-color);
}
.textareaContainer textarea:global.c-textarea-start-icon {
  position: absolute;
  left: 8px;
  display: flex;
  align-items: center;
  z-index: 1;
}
.textareaContainer textarea:global.c-textarea-start-icon + textarea {
  padding-left: var(--textarea-icon-padding);
}
.textareaContainer textarea:global.c-textarea-end-icon {
  position: absolute;
  right: 8px;
  display: flex;
  align-items: center;
  z-index: 1;
}
.textareaContainer textarea:global.c-textarea-end-icon + textarea {
  padding-right: var(--textarea-icon-padding);
}