.chatboxLayoutContainer {
  overflow-wrap: break-word; /* preferred over word-wrap */
  overflow: hidden;
  height: 100%;
}
.chatboxLayoutContainer :global .c-chatbox-layout-messages {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  gap: 0;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  padding: var(--c-padding);
}
.chatboxLayoutContainer :global .c-chatbox-layout-composer {
  padding-top: 10px;
}
.chatboxLayoutContainer :global .c-chatbox-layout-composer > div {
  position: sticky;
  bottom: 0;
}