.inputContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-wrap: nowrap;
  position: relative;
  width: 100%;
  height: var(--input-height);
  padding: var(--input-padding);
  border-radius: var(--input-border-radius);
  border: 1px solid var(--input-border-color);
  background-color: var(--input-background-color);
  transition: background-color 0.3s ease;
  box-sizing: border-box;
}
.inputContainer:focus-within {
  background-color: var(--input-focus-background-color);
  border: 1px solid var(--input-focus-border-color);
  outline: 0;
}
.inputContainer:hover {
  background-color: var(--input-focus-background-color);
  border: 1px solid var(--input-focus-border-color);
  outline: 0;
}
.inputContainer:global.c-input-disabled {
  color: var(--input-disabled-text-color);
  background-color: var(--input-disabled-background-color);
  border: 1px solid var(--input-disabled-border-color);
  cursor: not-allowed;
  pointer-events: none;
}
.inputContainer:global.c-input-error {
  color: var(--input-error-text-color);
  background-color: var(--input-error-background-color);
  border: 1px solid var(--input-error-border-color);
  outline: 1px solid var(--input-error-border-color);
}
.inputContainer :global .c-input {
  font-family: var(--font-family);
  font-size: var(--input-font-size);
  font-weight: var(--input-font-weight);
  line-height: var(--input-line-height);
  letter-spacing: var(--letter-spacing-base);
  color: var(--input-text-color);
  border: none;
  outline: none;
  width: 100%;
  background-color: transparent;
}
.inputContainer :global .c-input ::placeholder {
  color: var(--input-placeholder-color);
}
.inputContainer :global .c-input:focus {
  color: var(--input-focus-text-color);
}
.inputContainer :global .c-input:disabled {
  color: var(--input-disabled-text-color);
}
.inputContainer :global .c-input-start-icon {
  position: absolute;
  left: 8px;
  display: flex;
  align-items: center;
  z-index: 1;
}
.inputContainer :global .c-input-start-icon + input {
  padding-left: var(--input-icon-padding);
}
.inputContainer :global .c-input-end-icon {
  position: absolute;
  right: 8px;
  display: flex;
  align-items: center;
  z-index: 1;
}
.inputContainer :global .c-input-end-icon + input {
  padding-right: var(--input-icon-padding);
}